@import "../index.css";
.wrapper {
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.left,
.right {
  width: 50vw;
  height: 100vh;
  overflow-y: scroll;
}

.left {
  overflow: hidden;
}
.left .card:nth-child(odd) {
  background-color: var(--bg);
  color: var(--text);
}

.right .card:nth-child(even) {
  background-color: var(--bg);
  color: var(--text);
}

.card {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3.1vw;
}